import React, {Component} from 'react';
import PropTypes from 'prop-types';
import  {
    Col,
    Container,
    Row,
    InputGroup,
    Input,
    InputGroupAddon,
    Button,
    Table,
    Progress
} from "reactstrap";


class Todo extends Component {
    constructor(props) {
        super(props);
        this.state={
            work: "",
            todos: [],
            id: 0,
            fullName:this.props.Fisrtname + this.props.Lastname
        }
    }

    hendleInputChange=(event)=>{
        this.setState({
            work: event.target.value
        })
    }

    addtodo=()=>{
        if (this.state.work !==""){
        let todo={
            work: this.state.work,
            id: this.state.id,
            isComplete: false
        }
        this.setState({
            todos: this.state.todos.concat(todo),
            id: this.state.id + 1,
            work: ""
        })}
        else {
            alert("So'z kiritng")
        }
    }
    maketodo=(id)=>{
        this.setState({
            todos: this.state.todos.filter((item)=>{
                if (item.id === id){
                    item.isComplete =! item.isComplete
                }
                return item
            })
        })
    }

    delete=(id)=>{
        this.setState({
            todos: this.state.todos.filter((item)=>{
                if (item.id !==id){
                    return  item
                }
            })
        })
    }

    render() {
        return (
            <Container>
               <Row className="mt-5">
                   <Col md={{size: 6, offset: 3}}>
                       <h5>Add yoru todo list {this.state.fullName}</h5>
                       <InputGroup>
                           <Input value={this.state.work} placeholder="username"  onChange={this.hendleInputChange}/>
                           <InputGroupAddon addonType="append">
                               <Button color="primary" onClick={this.addtodo}>Add</Button>
                           </InputGroupAddon>
                       </InputGroup>
                   </Col>
               </Row>
                <Row>
                    <Col md={{size :6, offset :3}}>
                        <Table>
                            <tbody className='text-center'>
                        {this.state.todos.map((item)=>{
                            return <tr>
                             <td> <Input checked={item.isComplete} type="checkbox" onClick={()=>this.maketodo(item.id)} /></td>
                             <td>{item.work}</td>
                             <td><Button className='btn btn-sm' color="danger" onClick={()=>this.delete(item.id)}>Delete</Button></td>
                         </tr>
                        })}
                            </tbody>
                        </Table>
                    </Col>
                </Row>
                <Row>
                    <Col md={{size: 6, offset: 3}}>
                        <Progress  animated color="success" value={(this.state.todos.filter((item)=>{
                            if (item.isComplete)
                                return item
                        })).length*100/(this.state.todos).length} >
                            {(this.state.todos.filter((item)=>{
                                if (item.isComplete)
                                    return item
                            })).length*100/(this.state.todos).length}%
                        </Progress>
                    </Col>
                </Row>
            </Container>
        );
    }
}

Todo.propTypes = {};

export default Todo;