import React, {Component} from 'react';
import PropTypes from 'prop-types';

class App extends Component {

  constructor(props) {
    super(props);
    this.state={
      a: "a",
      b: "b",
      c: "c",
      result: "",
      plus: "+",
    }
  }

  abcSetUp=(event)=>{
    let b=this.state.b
    let plus= "+"
    if (b>0){
      this.setState({
        plus: "+"
      })
    }
    else if (b<0){
      this.setState({
        plus: " "
      })
    }
    this.setState({
      [event.target.name]:event.target.value
    })
  }

  calculateEq=()=>{

    let b=this.state.b
    let a=this.state.a;
    let c=this.state.c;
    this.setState({
      d: ""
    })

    let d=Math.sqrt(Math.pow(b,2)-4*a*c)
    let result=""
    if (d>0){
      let x1=(-b+d)/(2*a);
      let x2=(-b-d)/(2*a);
      result="x1: "+x1+"   x2: "+x2+""
    }
    else if (d===0){
      let x=-b/(2*a);
      result="1 ta yechimga ega ya'ni "+x+" ga teng"
    }
    else {
      result="Yechimga ega emas"
    }
    this.setState({
      result:result
    })
  }



  render() {
    return (
        <div>
          <h1>{this.state.a}x<sup>2</sup>  {this.state.plus}  {this.state.b}x +  {this.state.c}=0</h1>
          <input type="text" name="a" value={this.state.d} placeholder="a sonni kiriting" onChange={this.abcSetUp}/>
          <input type="text" name="b" value={this.state.d} placeholder="b sonni kiriting" onChange={this.abcSetUp}/>
          <input type="text" name="c" value={this.state.d} placeholder="c sonni kiriting" onChange={this.abcSetUp}/>
          <button onClick={this.calculateEq}>calculate</button>
          <h1>{this.state.result}</h1>





        </div>
    );
  }
}

App.propTypes = {};

export default App;
