import React, {Component} from 'react';
import PropTypes from 'prop-types';
import SignIn from "../props/signIn";
import {Switch, Route, Link} from "react-router-dom";
import Todo from "../todo";
import App from "../../App";
import Cabinet from "./cabinet";
import {CardBody,Card} from "reactstrap";

class Main extends Component {
    constructor(props) {
        super(props);

    }

    render() {
        return (
            <div>
                <Link to="/sign_in">SignIn</Link>
                <Link to="/">Cabinet</Link>
                <Link to="/todo">Todo</Link>
                <Link to="/image">
                    <Card>
                        <CardBody>
                            <p>
                                Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                                A, aliquam aliquid architecto aspernatur cupiditate ea eius necessitatibus, nihil
                                sequi temporibus ullam velit voluptates. Asperiores, commodi deleniti dolor ducimus harum sapiente?
                            </p>
                        </CardBody>
                    </Card>
                </Link>
                <Switch>
                    <Route exact path="/" component={Cabinet}/>
                    <Route exact path="/sign_in" component={SignIn}/>
                    <Route exact path="/todo" component={Todo}/>
                </Switch>
            </div>
        );
    }
}

Main.propTypes = {};

export default Main;