import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {Col, Input, Row, Container, Button, InputGroup} from "reactstrap";
import Todo from "../todo";

class SignIn extends Component {

    constructor(props) {
        super(props);
        this.state={
            isSuccess: false,
            name: ""
        }
    }

    handleInputChange=(event)=>{
        this.setState({
            name: event.target.value
        })
    }

    add=()=>{
        this.setState({
            isSuccess: true
        })
    }

    render() {
        let st=this.state
        return (
            <div>
                <Container>
                    {this.state.isSuccess ? <Todo Fisrtname={st.name} Lastname={st.name}/> : <Row>
                        <Col md={{size:6, offset:3}}>
                        <InputGroup>
                        <Input type="text" name="username"  placeholder="Enter username" onChange={this.handleInputChange} />
                        <Button  color="primary" onClick={this.add}>primary</Button>
                        </InputGroup>
                        </Col>
                        </Row>}
                </Container>
            </div>
        );
    }
}

SignIn.propTypes = {};

export default SignIn;